.about__container{
    display: grid;
    height: 90vh;
    
    grid-template-columns: 40% 50%;
    gap: 15%;
}

.about_me{
    width: 80%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg,transparent,var(--color-primary),transparent);
    display: grid;
    
    place-items: center;
}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}
.about__me-image:hover{
    transform: rotate(0);
}

.about_cards{
    display:grid ;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
   
    
}

.about__card{
    height: 150px;
    width: 150px;
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}
.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;

}

.about__card h5{
    font-size: 0.95rem;
}


.about__card small{
    font-size: 0.5rem;
    color: var(--color-light);
}

.about_content p {
    margin: 1rem;
    font-weight: 600;
    color: var(--color-light);
}

/* ======media====tab*/
@media screen and (max-width:1024px){
    .about__container{
        grid-template-columns:1fr;
        gap: 0;
        height: 130vh;
        align-items: center;
    }
    
    
    .about_me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
.about_cards{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem;
    justify-content: space-between;

}
    .about_content p{
        margin: 1rem 0 1.5rem;

    }
    
}

/* ======media====mob*/
@media screen and (max-width:600px){
    .about__container{
        
        max-height: fit-content;
        gap: 0%;
        height: 100vh;
    }
    .about_me{
        width: 45%;
        margin: 0 auto 3rem;
    }
    .about_cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    .about_content  {
        text-align: center;
        
    }
    .about_content p{
        margin: 1.5rem 0;
    }
}